"use client";
import { useMemo, useState } from "react";
import { useQuery } from "@tanstack/react-query";
import Panel from "@/components/Panel";
import StatCard from "@/components/StatCard";
import MemberCard from "@/components/MemberCard";
import PageHeader from "@/components/layout/PageHeader";
import MemberDrawer from "@/components/MemberDrawer";
import TotalBalanceDrawer from "@/components/home/TotalBalanceDrawer";
import YearlyPeek from "@/components/home/YearlyPeek";
import { api } from "@/lib/clientApi";
import { formatBDT } from "@/lib/format";
import type { ServerMe } from "@/lib/server/me";
import type { HomeResponse, MemberSummary } from "@/types/home";
import { useRouter } from "next/navigation";
import { AnimatePresence } from "framer-motion";

type Props = {
  initialData: HomeResponse;
  me: ServerMe;
};

const STALE_TIME = 30_000;

export default function HomeClient({ initialData, me }: Props) {
  const router = useRouter();
  const [drawerUserId, setDrawerUserId] = useState<string | null>(null);
  const [showTotalsDrawer, setShowTotalsDrawer] = useState(false);
  const home = useQuery<HomeResponse>({
    queryKey: ["home"],
    queryFn: async () => (await api.get("/home")).data,
    initialData,
    staleTime: STALE_TIME,
    gcTime: STALE_TIME * 4,
    refetchOnWindowFocus: false,
  });
  const data = home.data;
  const isRefreshing = home.isFetching && !!home.data;

  const duesQuery = useQuery<any[]>({
    queryKey: ["dues", me.id],
    queryFn: async () => (await api.get(`/users/${me.id}/dues`)).data,
    enabled: me.role === "user" && !!me.id,
    staleTime: STALE_TIME,
    refetchOnWindowFocus: false,
  });

  const userTxsQuery = useQuery<any[]>({
    queryKey: ["txs", me.id, 12],
    queryFn: async () => (await api.get("/transactions", { params: { userId: me.id, limit: 20 } })).data,
    enabled: me.role === "user" && !!me.id,
    staleTime: STALE_TIME,
    refetchOnWindowFocus: false,
  });

  const nextEmi = useMemo(() => {
    if (me.role !== "user" || !duesQuery.data) return 0;
    let soonest: number | null = null;
    for (const due of duesQuery.data) {
      for (const item of due.schedule) {
        if (item.status === "paid") continue;
        const remaining = (item.totalDuePoisha || 0) - (item.paidPoisha || 0);
        if (remaining <= 0) continue;
        soonest = remaining;
        break;
      }
      if (soonest) break;
    }
    return soonest || 0;
  }, [duesQuery.data, me.role]);

  if (!data) return null;

  if (me.role === "user" && data.cards.length) {
    const card = data.cards[0];
    const recent = userTxsQuery.data || card.recent || [];
    return (
      <div className="space-y-6">
        <PageHeader
          eyebrow="My savings"
          title={`Hi ${me.name || "there"}, here’s the current picture`}
          description="Balance, what you’ve contributed so far, and upcoming instalments if any are still pending."
        />

        <div className="grid grid-cols-1 gap-3 sm:grid-cols-2 lg:grid-cols-3">
          <StatCard label="Savings balance" value={formatBDT(data.groupBalance)} />
          <StatCard label="Total contributed" value={formatBDT(data.totalDeposits)} />
          {nextEmi > 0 ? <StatCard label="Next payment due" value={formatBDT(nextEmi)} helper="Covers your oldest pending instalment" /> : null}
        </div>

        {isRefreshing ? <p className="text-xs text-slate-500">Refreshing your dashboard…</p> : null}

        <Panel title="Recent activity" description="Only transactions tied to your name appear here.">
          <div className="grid grid-cols-12 text-xs text-slate-500 px-2">
            <div className="col-span-3">Date</div>
            <div className="col-span-5">Details</div>
            <div className="col-span-4 text-right">Amount</div>
          </div>
          <div className="max-h-72 overflow-auto divide-y divide-slate-100 dark:divide-slate-800">
            {recent.length
              ? recent.map((tx: any) => (
                  <div key={tx._id || tx.date} className="grid grid-cols-12 items-center py-2 px-2 text-sm">
                    <span className="col-span-3">{new Date(tx.occurredAt || tx.date).toLocaleDateString("en-BD")}</span>
                    <span className={`col-span-5 ${tx.type === "deposit" ? "text-emerald-600" : "text-rose-600"}`}>{tx.note || tx.type}</span>
                    <span className="col-span-4 text-right">{formatBDT(tx.amount)}</span>
                  </div>
                ))
              : (
                <div className="text-sm text-slate-500 px-2 py-3">No transactions recorded yet.</div>
              )}
          </div>
        </Panel>
      </div>
    );
  }

  const summaryStats = useMemo(() => {
    if (!data) return [];
    const stats: Array<{ label: string; value: string | number; helper?: string; onClick?: () => void }> = [
      {
        label: "Total balance",
        value: formatBDT(data.groupBalance),
        helper: "Tap to compare deposits vs. deductions",
        onClick: () => setShowTotalsDrawer(true),
      },
      {
        label: "Available cash",
        value: formatBDT(data.groupBalance),
        helper: "See every member’s balance",
        onClick: () => router.push("/balances"),
      },
      { label: "Active members", value: data.membersCount, helper: "Counting only active profiles" },
      { label: "Open dues", value: data.arrearsCount, helper: "Members behind on instalments" },
    ];
    const investmentInfo = data.investments || { principal: 0, expectedInterest: 0 };
    stats.push(
      { label: "Invested funds", value: formatBDT(investmentInfo.principal), helper: "Currently out earning returns" },
      { label: "Expected interest", value: formatBDT(investmentInfo.expectedInterest) },
      { label: "Total collected", value: formatBDT(data.totalDeposits) }
    );
    return stats;
  }, [data, router]);

  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 gap-3 sm:grid-cols-2 xl:grid-cols-4">
        {summaryStats.map((stat) => (
          <StatCard key={String(stat.label)} label={stat.label} value={stat.value} helper={stat.helper} onClick={stat.onClick} />
        ))}
      </div>

      {me.role !== "user" ? <YearlyPeek /> : null}

      {isRefreshing ? <p className="text-xs text-slate-500">Refreshing data…</p> : null}

      <Panel title="Members at a glance" description="Open any member to record deposits, withdrawals, or review their ledger.">
        <div className="grid grid-cols-1 gap-3 sm:grid-cols-2 xl:grid-cols-3">
          {data.cards.map((card) => (
            <MemberCard key={card.userId} member={card} onSelect={(id) => setDrawerUserId(id)} />
          ))}
        </div>
      </Panel>

      <AnimatePresence>
        {drawerUserId ? <MemberDrawer userId={drawerUserId} role={me.role} onClose={() => setDrawerUserId(null)} /> : null}
      </AnimatePresence>
      <AnimatePresence>
        {showTotalsDrawer ? (
          <TotalBalanceDrawer
            open={showTotalsDrawer}
            totalDeposits={data.totalDeposits}
            totalWithdraws={data.totalWithdraws}
            available={data.groupBalance}
            rows={data.cards}
            onClose={() => setShowTotalsDrawer(false)}
          />
        ) : null}
      </AnimatePresence>
    </div>
  );
}
