"use client";
import { ReactNode } from "react";
import { motion } from "framer-motion";

type Props = {
  label: ReactNode;
  value: ReactNode;
  helper?: ReactNode;
  icon?: ReactNode;
  onClick?: () => void;
};

export default function StatCard({ label, value, helper, icon, onClick }: Props) {
  const CardElement: typeof motion.div | typeof motion.button = onClick ? motion.button : motion.div;
  return (
    <CardElement
      type={onClick ? "button" : undefined}
      onClick={onClick}
      className={`rounded-2xl border border-white/60 dark:border-[#2b1f4a] bg-gradient-to-br from-[#FDF5FF] via-[#E9A5F1]/60 to-[#C68EFD]/40 dark:from-[#281745] dark:via-[#1f1136] dark:to-[#120926] p-5 shadow-lg shadow-[#C68EFD]/20 transition ${
        onClick ? "text-left focus:outline-none focus:ring-2 focus:ring-blue-400/40 cursor-pointer" : ""
      }`}
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.2 }}
      whileHover={{ y: -4 }}
      whileTap={onClick ? { scale: 0.98 } : undefined}
    >
      <div className="flex items-start gap-3">
        {icon ? <div className="text-blue-600 dark:text-blue-300">{icon}</div> : null}
        <div>
          <p className="text-xs uppercase tracking-[0.3em] text-slate-400">{label}</p>
          <p className="text-2xl font-semibold text-slate-900 dark:text-white mt-2">{value}</p>
          {helper ? <p className="text-xs text-slate-500 dark:text-slate-400 mt-1">{helper}</p> : null}
        </div>
      </div>
    </CardElement>
  );
}
