"use client";
import { useQuery } from "@tanstack/react-query";
import { motion, useReducedMotion } from "framer-motion";
import Panel from "@/components/Panel";
import StatCard from "@/components/StatCard";
import { SkeletonList } from "@/components/Skeleton";
import { api, formatBDT } from "@/lib/clientApi";
import type { MemberSummary } from "@/types/home";
import { useLockBodyScroll } from "@/hooks/useLockBodyScroll";

type Props = {
  open: boolean;
  totalDeposits: number;
  totalWithdraws: number;
  available: number;
  rows: MemberSummary[];
  onClose: () => void;
};

type TransactionSnapshot = { _id: string; note?: string; occurredAt: string; amount: number };
type InvestmentSummary = {
  id: string;
  name: string;
  amountPoisha: number;
  expectedInterestPoisha?: number;
  returnedPoisha?: number;
  status: "active" | "completed";
};

export default function TotalBalanceDrawer({ open, totalDeposits, totalWithdraws, available, rows, onClose }: Props) {
  useLockBodyScroll(open);

  const withdraws = useQuery<TransactionSnapshot[]>({
    queryKey: ["withdrawals", "totals"],
    queryFn: async () => (await api.get("/transactions", { params: { type: "withdraw", limit: 20 } })).data,
    enabled: open,
  });

  const investments = useQuery<InvestmentSummary[]>({
    queryKey: ["investments", "totals"],
    queryFn: async () => (await api.get("/investments")).data,
    enabled: open,
  });

  if (!open) return null;
  const net = totalDeposits - totalWithdraws;

  const reduceMotion = useReducedMotion();
  const sheetVariants = reduceMotion
    ? { initial: { opacity: 0 }, animate: { opacity: 1 }, exit: { opacity: 0 } }
    : {
        initial: { y: "100%" },
        animate: { y: 0, transition: { duration: 0.32, ease: [0.16, 1, 0.3, 1] as const } },
        exit: { y: "100%", transition: { duration: 0.22, ease: [0.4, 0, 1, 1] as const } },
      };

  return (
    <motion.div className="fixed inset-0 z-40 bg-gradient-to-b from-white via-emerald-50 to-slate-50 dark:from-slate-950 dark:via-slate-900 dark:to-slate-950 overflow-y-auto" variants={sheetVariants} initial="initial" animate="animate" exit="exit">
      <div className="w-full min-h-screen px-4 py-6 sm:px-6">
        <div className="flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between mb-4">
          <div>
            <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Total balance</p>
            <h2 className="text-2xl font-semibold text-slate-900 dark:text-white">Collections vs. deductions</h2>
          </div>
          <button type="button" className="text-sm text-slate-500 hover:text-rose-500" onClick={onClose}>
            Close
          </button>
        </div>

        <div className="grid grid-cols-1 gap-3 sm:grid-cols-2 xl:grid-cols-4 mb-4">
          <StatCard label="Collected overall" value={formatBDT(totalDeposits)} />
          <StatCard label="Deducted / invested" value={formatBDT(totalWithdraws)} />
          <StatCard label="Net balance" value={formatBDT(net)} />
          <StatCard label="Available cash" value={formatBDT(available)} />
        </div>

        <div className="grid grid-cols-1 gap-6 lg:grid-cols-2">
          <Panel title="Per-member totals" description="Lifetime deposits vs. deductions.">
            <div className="overflow-auto max-h-72 pr-1">
              <table className="min-w-[520px] text-sm">
                <thead>
                  <tr className="text-left text-slate-500 dark:text-slate-400">
                    <th className="py-2 pr-3 font-medium">Member</th>
                    <th className="py-2 px-3 font-medium text-right">Deposited</th>
                    <th className="py-2 px-3 font-medium text-right">Deducted</th>
                    <th className="py-2 pl-3 font-medium text-right">Balance</th>
                  </tr>
                </thead>
                <tbody>
                  {rows.map((row) => (
                    <tr key={row.userId} className="border-t border-slate-100 dark:border-slate-800">
                      <td className="py-2 pr-3 font-medium text-slate-900 dark:text-white">{row.name}</td>
                      <td className="py-2 px-3 text-right">{formatBDT(row.totalDeposits || 0)}</td>
                      <td className="py-2 px-3 text-right">{formatBDT(row.totalWithdraws || 0)}</td>
                      <td className="py-2 pl-3 text-right font-semibold text-emerald-600 dark:text-emerald-300">{formatBDT(row.balance || 0)}</td>
                    </tr>
                  ))}
                  {!rows.length && (
                    <tr>
                      <td colSpan={4} className="py-3 text-center text-slate-500">
                        No members yet.
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </Panel>

          <Panel title="Recent withdrawals / investments" description="Latest 20 records">
            {withdraws.isLoading ? (
              <SkeletonList rows={4} />
            ) : withdraws.data?.length ? (
              <div className="space-y-2 max-h-72 overflow-auto pr-1">
                {withdraws.data.map((tx) => (
                  <div key={tx._id} className="rounded-2xl border border-slate-200 dark:border-slate-800 p-3 flex items-center justify-between">
                    <div>
                      <p className="text-sm font-semibold text-slate-900 dark:text-white">{tx.note || "Withdrawal"}</p>
                      <p className="text-xs text-slate-500 dark:text-slate-400">{new Date(tx.occurredAt).toLocaleDateString("en-BD")}</p>
                    </div>
                    <p className="text-sm font-semibold text-rose-600 dark:text-rose-300">{formatBDT(Math.abs(tx.amount || 0))}</p>
                  </div>
                ))}
              </div>
            ) : (
              <p className="text-sm text-slate-500">No withdrawals recorded yet.</p>
            )}
          </Panel>
        </div>

        <Panel title="Investments overview" description="Funds currently out of circulation.">
          {investments.isLoading ? (
            <SkeletonList rows={3} />
          ) : investments.data?.length ? (
            <div className="grid grid-cols-1 gap-3 sm:grid-cols-2">
              {investments.data.map((inv) => {
                const returned = inv.returnedPoisha || 0;
                const total = inv.amountPoisha + (inv.expectedInterestPoisha || 0);
                const pct = total ? Math.min(100, Math.round((returned / total) * 100)) : 0;
                return (
                  <div key={inv.id} className="rounded-2xl border border-slate-200 dark:border-slate-800 p-4 space-y-2">
                    <div className="flex items-center justify-between">
                      <p className="font-semibold text-slate-900 dark:text-white">{inv.name}</p>
                      <span className="text-xs px-2 py-0.5 rounded-full border border-slate-200 dark:border-slate-700 text-slate-500 dark:text-slate-300">
                        {inv.status === "completed" ? "Completed" : "Active"}
                      </span>
                    </div>
                    <p className="text-sm text-slate-500 dark:text-slate-400">Principal: {formatBDT(inv.amountPoisha)}</p>
                    <div className="text-xs text-slate-500 dark:text-slate-400">
                      Returned {formatBDT(returned)} of {formatBDT(total)} ({pct}%)
                    </div>
                    <div className="w-full h-1.5 rounded-full bg-slate-100 dark:bg-slate-800 overflow-hidden">
                      <div className="h-full bg-emerald-500" style={{ width: `${pct}%` }} />
                    </div>
                  </div>
                );
              })}
            </div>
          ) : (
            <p className="text-sm text-slate-500">No investments recorded yet.</p>
          )}
        </Panel>
      </div>
    </motion.div>
  );
}
