"use client";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { api, formatBDT } from "@/lib/clientApi";

export default function YearlyPeek() {
  const router = useRouter();
  const currentYear = new Date().getFullYear();
  const years = [currentYear, currentYear - 1];
  const [totals, setTotals] = useState<Record<number, number>>({});
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    let cancelled = false;
    async function load() {
      setLoading(true);
      setError(null);
      try {
        const results = await Promise.all(
          years.map(async (year) => {
            const res = await api.get("/reports/yearly-collection", { params: { year } });
            return { year, total: res.data?.total ?? 0 };
          })
        );
        if (!cancelled) {
          setTotals(results.reduce((acc, item) => ({ ...acc, [item.year]: item.total }), {}));
        }
      } catch (err: any) {
        if (!cancelled) setError(err?.response?.data?.error || "Failed to load yearly totals");
      } finally {
        if (!cancelled) setLoading(false);
      }
    }
    load();
    return () => {
      cancelled = true;
    };
  }, [currentYear]);

  return (
    <section className="space-y-3">
      <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Yearly collection</p>
      <div className="grid grid-cols-1 gap-3 sm:grid-cols-2">
        {years.map((year) => {
          const total = totals[year];
          return (
            <button
              key={year}
              type="button"
              onClick={() => router.push(`/yearly?year=${year}`)}
              className="text-left rounded-2xl border border-slate-200 dark:border-slate-800 bg-white/80 dark:bg-slate-900/70 p-4 shadow-sm transition hover:shadow-md focus:outline-none focus:ring-2 focus:ring-blue-400/40"
            >
              <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Collection year</p>
              <p className="text-2xl font-semibold text-slate-900 dark:text-white mt-1">{year}</p>
              <p className="text-sm text-slate-500 dark:text-slate-400 mt-2">
                {typeof total === "number" ? formatBDT(total) : loading ? "Loading…" : "Tap to view details"}
              </p>
            </button>
          );
        })}
      </div>
      {error ? <p className="text-sm text-rose-500">{error}</p> : null}
    </section>
  );
}
