"use client";
import { useEffect, useMemo, useState } from "react";
import Button from "@/components/Button";
import { getBrowserApiBase } from "@/lib/api";
import { formatBDT } from "@/lib/format";

type Props = {
  userId: string;
};

type Due = {
  _id: string;
  principal: number;
  months: number;
  monthlyRatePct: number;
  penaltyRule?: { graceDays?: number; monthlyPenaltyPct?: number; enabled?: boolean };
  schedule: Array<{ dueDate: string; totalDuePoisha: number; paidPoisha?: number; status: string }>;
};

export default function DepositForm({ userId }: Props) {
  const [mode, setMode] = useState<"simple" | "pay_due" | "investment_return">("simple");
  const [amount, setAmount] = useState("");
  const [date, setDate] = useState(() => new Date().toISOString().slice(0, 10));
  const [note, setNote] = useState("Deposit");
  const [includePenalty, setIncludePenalty] = useState(true);
  const [dues, setDues] = useState<Due[]>([]);
  const [dueId, setDueId] = useState<string | null>(null);
  const [loadingDues, setLoadingDues] = useState(false);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [investments, setInvestments] = useState<Array<{ id: string; name: string; status: string; amountPoisha: number; returnedPoisha?: number }>>([]);
  const [investmentId, setInvestmentId] = useState<string | null>(null);
  const [loadingInvestments, setLoadingInvestments] = useState(false);
  const [investmentError, setInvestmentError] = useState<string | null>(null);

  useEffect(() => {
    setAmount("");
    setDueId(null);
  }, [userId]);

  useEffect(() => {
    if (mode !== "pay_due") return;
    setLoadingDues(true);
    setError(null);
    fetch(`${getBrowserApiBase()}/users/${userId}/dues`, { credentials: "include" })
      .then(async (res) => {
        if (!res.ok) throw new Error("Failed to load dues");
        const list = (await res.json()) as Due[];
        setDues(list);
        if (list.length && !dueId) setDueId(list[0]._id);
      })
      .catch((err: any) => setError(err?.message || "Failed to load dues"))
      .finally(() => setLoadingDues(false));
  }, [mode, userId, dueId]);

  const hasOpenDues = dues.length > 0;
  useEffect(() => {
    if (mode === "pay_due" && hasOpenDues && !dueId) setDueId(dues[0]._id);
  }, [mode, hasOpenDues, dues, dueId]);

  useEffect(() => {
    if (mode !== "investment_return") return;
    setLoadingInvestments(true);
    setInvestmentError(null);
    fetch(`${getBrowserApiBase()}/investments`, { credentials: "include" })
      .then(async (res) => {
        if (!res.ok) throw new Error((await res.json())?.error || "Failed to load investments");
        const list = await res.json();
        setInvestments(list);
        if (list.length) setInvestmentId(list[0].id);
      })
      .catch((err: any) => setInvestmentError(err?.message || "Failed to load investments"))
      .finally(() => setLoadingInvestments(false));
  }, [mode]);

  useEffect(() => {
    setNote((prev) => {
      if (mode === "investment_return" && prev === "Deposit") return "Investment return";
      if (mode !== "investment_return" && prev === "Investment return") return "Deposit";
      return prev;
    });
  }, [mode]);

  const selected = useMemo(() => dues.find((d) => d._id === dueId), [dues, dueId]);
  const suggested = useMemo(() => {
    if (!selected) return 0;
    const today = new Date(date);
    const grace = selected.penaltyRule?.graceDays ?? 3;
    const pct = selected.penaltyRule?.monthlyPenaltyPct ?? 1;
    for (const item of selected.schedule) {
      if (item.status === "paid") continue;
      const base = (item.totalDuePoisha || 0) - (item.paidPoisha || 0);
      if (base <= 0) continue;
      const dueDate = new Date(item.dueDate);
      dueDate.setDate(dueDate.getDate() + grace);
      let total = base;
      if (includePenalty && today > dueDate && selected.penaltyRule?.enabled) {
        total += Math.floor((item.totalDuePoisha * pct) / 100);
      }
      return total;
    }
    return 0;
  }, [selected, includePenalty, date]);

  const onSubmit = async () => {
    if (!userId) return;
    setSaving(true);
    setError(null);
    try {
      if (mode === "investment_return") {
        if (!investmentId) throw new Error("Pick an investment to credit the return against.");
        const amtTaka = Number(amount);
        if (!amtTaka || !Number.isFinite(amtTaka)) throw new Error("Enter the amount that came back.");
        const res = await fetch(`${getBrowserApiBase()}/investments/${investmentId}/return`, {
          method: "POST",
          credentials: "include",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ amount: amtTaka, date, note }),
        });
        if (!res.ok) throw new Error((await res.json())?.error || "Failed to record investment return");
        setAmount("");
        setMode("simple");
        setInvestmentId(null);
        return;
      }

      const payload: any = { userId, mode, date, note, includePenalty };
      const amtTaka = Number(amount);
      if (amount) payload.amount = Number.isFinite(amtTaka) ? amtTaka : 0;
      else payload.amountPoisha = suggested || 0;
      if (mode === "pay_due") payload.dueId = dueId;
      const res = await fetch(`${getBrowserApiBase()}/deposit`, {
        method: "POST",
        credentials: "include",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });
      if (!res.ok) throw new Error((await res.json())?.error || "Deposit failed");
      setAmount("");
    } catch (err: any) {
      setError(err?.message || "Deposit failed");
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="space-y-6">
      <section className="rounded-2xl border border-slate-200 dark:border-slate-800 p-4 bg-white/70 dark:bg-slate-900/40 space-y-4">
        <header className="flex flex-col gap-2">
          <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Step 1</p>
          <h3 className="text-base font-semibold text-slate-900 dark:text-white">Choose the intent</h3>
        </header>
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-3">
          <ModeCard title="Simple deposit" body="Keeps the balance free for future withdrawals." active={mode === "simple"} onClick={() => setMode("simple")} />
          <ModeCard
            title="Pay down a due"
            body={hasOpenDues ? "Apply this to their repayment schedule." : "No open dues right now."}
            active={mode === "pay_due"}
            onClick={() => setMode("pay_due")}
            disabled={!hasOpenDues}
          />
          <ModeCard
            title="Investment return"
            body="Re-add money that just came back from an investment."
            active={mode === "investment_return"}
            onClick={() => setMode("investment_return")}
          />
        </div>

        {mode === "pay_due" && (
          <div className="rounded-2xl border border-blue-100 dark:border-blue-500/30 bg-blue-50/60 dark:bg-blue-500/10 p-4 space-y-3">
            <div className="flex items-center justify-between gap-3">
              <h4 className="text-sm font-semibold text-slate-900 dark:text-white">Target a due</h4>
              <span className="text-xs text-blue-600 dark:text-blue-200 font-medium bg-blue-100/70 dark:bg-blue-500/20 px-2 py-0.5 rounded-full">
                {loadingDues ? "Loading…" : `${dues.length} open`}
              </span>
            </div>
            <select className="input-like" value={dueId || ""} onChange={(e) => setDueId(e.target.value)}>
              <option value="">Select due…</option>
              {dues.map((d) => (
                <option key={d._id} value={d._id}>
                  Principal {formatBDT(d.principal)} — {d.months} mo @ {d.monthlyRatePct}%
                </option>
              ))}
            </select>
            <label className="inline-flex items-center gap-2 text-sm">
              <input type="checkbox" checked={includePenalty} onChange={(e) => setIncludePenalty(e.target.checked)} />
              Add penalty when grace period is over
            </label>
            <div className="text-xs text-blue-700 dark:text-blue-200 bg-white/60 dark:bg-slate-900/40 rounded-xl px-3 py-2 inline-flex items-center gap-2">
              Suggested instalment: <strong>{formatBDT(suggested)}</strong>
            </div>
          </div>
        )}

        {mode === "investment_return" && (
          <div className="rounded-2xl border border-emerald-200 dark:border-emerald-500/40 bg-emerald-50/60 dark:bg-emerald-500/10 p-4 space-y-3">
            <div className="flex items-center justify-between gap-3">
              <h4 className="text-sm font-semibold text-slate-900 dark:text-white">Select the investment</h4>
              <span className="text-xs text-emerald-700 dark:text-emerald-200 font-medium bg-white/70 dark:bg-slate-900/40 px-2 py-0.5 rounded-full">
                {loadingInvestments ? "Loading…" : `${investments.length} recorded`}
              </span>
            </div>
            <select className="input-like" value={investmentId || ""} onChange={(e) => setInvestmentId(e.target.value || null)}>
              <option value="">Pick an investment…</option>
              {investments.map((inv) => (
                <option key={inv.id} value={inv.id}>
                  {inv.name} · {inv.status === "completed" ? "Completed" : "Active"} ({formatBDT(inv.amountPoisha)})
                </option>
              ))}
            </select>
            {investmentError ? <p className="text-sm text-rose-500">{investmentError}</p> : null}
            {!investments.length && !loadingInvestments ? <p className="text-sm text-slate-500">No investments recorded yet.</p> : null}
          </div>
        )}
      </section>

      <section className="rounded-2xl border border-slate-200 dark:border-slate-800 p-4 bg-white/80 dark:bg-slate-900/50 space-y-4">
        <header>
          <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Step 2</p>
          <h3 className="text-base font-semibold text-slate-900 dark:text-white">Fill in the details</h3>
        </header>
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
          <Field label="Amount (BDT)">
            <input className="input-like" type="number" step="0.01" placeholder="0.00" value={amount} onChange={(e) => setAmount(e.target.value)} />
          </Field>
          <Field label="Date">
            <input className="input-like" type="date" value={date} onChange={(e) => setDate(e.target.value)} />
          </Field>
        </div>
        <Field label="Note">
          <input className="input-like" value={note} onChange={(e) => setNote(e.target.value)} />
        </Field>
        {error ? <p className="text-sm text-rose-500">{error}</p> : null}
      </section>

      <div className="flex justify-end">
        <Button onClick={onSubmit} disabled={saving}>
          {saving ? "Saving…" : "Save deposit"}
        </Button>
      </div>
    </div>
  );
}

function Field({ label, children }: { label: string; children: React.ReactNode }) {
  return (
    <div className="space-y-1">
      <label className="text-sm font-medium text-slate-600 dark:text-slate-300">{label}</label>
      {children}
    </div>
  );
}

function ModeCard({ title, body, active, onClick, disabled }: { title: string; body: string; active: boolean; onClick: () => void; disabled?: boolean }) {
  return (
    <button
      type="button"
      onClick={onClick}
      disabled={disabled}
      className={`rounded-2xl border p-4 text-left transition disabled:opacity-60 disabled:cursor-not-allowed ${
        active
          ? "border-blue-400 bg-blue-50 text-blue-700 dark:border-blue-500 dark:bg-blue-500/10 dark:text-blue-200"
          : "border-slate-200 dark:border-slate-700 hover:border-blue-200 hover:bg-blue-50/40 dark:hover:border-blue-500/40 dark:hover:bg-slate-800"
      }`}
    >
      <p className="font-semibold">{title}</p>
      <p className="text-sm text-slate-500 dark:text-slate-400">{body}</p>
    </button>
  );
}
