"use client";
import { formatBDT } from "@/lib/format";
import { motion } from "framer-motion";
import type { MemberSummary } from "@/types/home";

type Props = {
  member: Pick<MemberSummary, "userId" | "name" | "balance" | "lastMonth"> & {
    totalDeposits?: number;
    totalWithdraws?: number;
  };
  onSelect?: (userId: string) => void;
};

export default function MemberCard({ member, onSelect }: Props) {
  const handleClick = () => {
    if (onSelect) onSelect(member.userId);
  };
  const Card = motion.button;
  return (
    <Card
      type="button"
      onClick={handleClick}
      className={`w-full text-left rounded-2xl border border-white/70 dark:border-[#2b1f4a] bg-gradient-to-br from-[#FDF5FF]/90 via-[#E9A5F1]/60 to-[#C68EFD]/50 dark:from-[#271444]/80 dark:via-[#1f0f37]/60 dark:to-[#140a25]/60 p-4 shadow-lg shadow-[#8F87F1]/15 ${
        onSelect ? "focus:outline-none focus:ring-2 focus:ring-[#C68EFD]/40" : ""
      }`}
      whileHover={{ y: -4, boxShadow: "0 20px 30px rgba(143,135,241,0.25)" }}
      whileTap={{ scale: 0.98 }}
      transition={{ duration: 0.2, ease: [0.16, 1, 0.3, 1] }}
    >
      <div className="flex items-center justify-between">
        <div>
          <p className="text-base font-semibold text-slate-900 dark:text-white">{member.name}</p>
          <p className="text-xs text-slate-500 dark:text-slate-400">Last month {formatBDT(member.lastMonth || 0)}</p>
        </div>
        <div className="text-right">
          <p className="text-xs text-slate-500 dark:text-slate-400">Balance</p>
          <p className="text-sm font-semibold text-emerald-600 dark:text-emerald-300">{formatBDT(member.balance || 0)}</p>
        </div>
      </div>
    </Card>
  );
}
